body {
	font-family: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
	background-color: #111;
	color: #fff;
	transition: .3s linear;
}

body.dark {
	background-color: #111;

	a {
		color: #dd5644;
	}

	a:hover {
		color: #fff;
	}

	strong {
		color: #dd5644;
	}

	.header {
		&__link {
			color: #dd5644;
		}
	}

	.project-card {
		box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.05);
		&__stack {
			background-color: #4a4a4ab3;
			color: #fff;
		}

		&__refs {

			a {
				background-color: #dd5644;
				color: #fff;
			}
	
			a:hover {
				background-color: #9b2f20;
				text-decoration: none;
			}
		}
	}
	
}

body.light {
	background-color: #F0E3CA;
	color: #1B1A17;

	.title,
	.subtitle {
		color: #1B1A17;
	}

	.header {
		&__link {
			color: #1B1A17;

			&:hover {
				color: #ff8100;
			}
		}
	}

	.project-card {
		box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.05);
		&__stack {
			background-color: #4a4a4ab3;
			color: #fff;
		}

		&__refs {

			a {
				background-color: #f98e20;
				color: #fff;
			}
	
			a:hover {
				background-color: #c96600;
				text-decoration: none;
			}
		}
	}

	.contacts p a {
		color: #ff8100;

		&:hover {
			color: #c96600;
		}
	}
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

a {
    text-decoration: underline;
	color: #dd5644;
	transition: color .2s ease-in-out;
}

a:hover {
	color: #fff;
}

strong {
	color: #dd5644;
}

ul {
	list-style-type: none;
	padding-left: 0;
}

.container {
	max-width: 1200px;
	padding: 0 15px 30px;
	margin: 0 auto;
}

.title {
	font-size: 95px;
	font-weight: bold;
	margin: 10px 0;
	text-align: center;
}

.subtitle {
	font-size: 50px;
	margin: 60px 0 30px;
	text-align: center;
}

.description {
	font-size: 25px;
}

.warn {
	font-size: 35px;
}

.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	max-width: 1200px;
	padding: 30px 15px;
	margin: 0 auto;

	font-size: 50px;

	&__link {
		text-decoration: none;
		padding: 5px 10px;
		border: 5px solid;
		display: block;
		font-weight: bold;
		color: #dd5644;
	}
}

.projects,
.mini-projects {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.mini-projects .project-card__img {
	width: 292px;
	height: 212px;
}

.project-card {
	margin: 10px 8px;
	box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.05);

	&__img {
		border-radius: 7px 7px 0 0;
		width: 366px;
		height: 266px;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__img:hover {
		.project-card__stack {
			opacity: 1;
		}
	}

	&__stack {
		border-radius: 7px 7px 0 0;
		background-color: #4a4a4ab3;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;

		opacity: 0;
		font-size: 25px;
		color: #fff;
		transition: opacity .5s ease-in-out;
		cursor: default;
		
		text-align: center;
	}

	&__title {
		margin-top: 5px;
		margin-bottom: 25px;
	}

	&__refs {
		display: flex;
		justify-content: space-between;
		align-items: center;

		a {
			width: 50%;
			text-align: center;
			background-color: #dd5644;
			padding: 10px;

			color: #fff;
			text-decoration: none;
			font-size: 20px;

			transition: background-color .3s ease-in-out;

			&:first-child {
				border-radius: 0 0 0 7px;
			}

			&:last-child {
				border-radius: 0 0 7px 0;
			}
		}

		a:hover {
			background-color: #9b2f20;
			text-decoration: none;
		}
	}
}

.skills {
	font-size: 35px;
	margin: 0;

	li {
		margin-bottom: 15px;
	}

	li:last-child {
		margin-bottom: 0;
	}
}

.contacts {
	font-size: 35px;
	display: flex;
	flex-wrap: wrap;
}

.contacts p {
	width: 50%;
	margin-bottom: 10px;
}

// DarkMode

#darkMode {
	background: transparent url("./assets/img/dark-mode/moon.svg") no-repeat center center/contain;
	width: 64px;
	height: 64px;
	border: none;
	cursor: pointer;
	transition: transform .3s ease-in-out;

	&:hover {
		transform: rotate(20deg);
	}
}
  
#darkMode.clicked {
	background: transparent url("./assets/img/dark-mode/sun.svg") no-repeat center center/contain;
}

@media (max-width: 600px) {
	.title {
		font-size: 60px;
	}

	.subtitle {
		font-size: 40px;
	}

	.description {
		font-size: 25px;
	}

	.warn {
		font-size: 25px;
	}

	.description {
		font-size: 20px;
	}

	.skills {
		font-size: 25px;
	}

	.contacts p {
		font-size: 30px;
		margin: 15px 0;
		width: 100%;
	}

	.project-card__title {
		font-size: 25px;
	}

	.project-card__stack {
		font-size: 20px;
	}
}

@media (max-width: 425px) {
	.title {
		font-size: 55px;
	}

	.subtitle {
		font-size: 35px;
	}

	.warn {
		font-size: 20px;
	}

	.skills {
		font-size: 20px;
	}

	.contacts p {
		font-size: 20px;
	}

	.project-card__img {
		width: 292px;
		height: 212px;
	}

	.project-card__title {
		font-size: 20px;
	}

	.header__link {
		font-size: 40px;
		padding: 0px 5px;
	}

	#darkMode {
		width: 50px;
		height: 50px;
	}
}

